async function fetchOptions(types) {

    try {
        let url = new URL('https://wordpress-react-test.local/wp-json/booker67/v1/options/filter');
        types.forEach(type => url.searchParams.append('types[]', type));
        const response = await fetch(url);

        if (!response.ok) {
            throw new Error('Erreur rseau ou serveur');
        }

        const data = await response.json();
        console.log('Options rcupres:', data);
        return data;
    } catch (error) {
        console.error('Erreur lors de la rcupration des options:', error);
    }
}

// Exposez la fonction pour qu'elle soit utilisable  l'extrieur du fichier
export default fetchOptions;
