// URL de base pour les requtes API
const BASE_URL = '/wp-json/booker67/v1';

/**
 * Vrifie si une nouvelle option peut tre insre en fonction de maxAllowed.
 *
 * @param {string} genericType - Le type gnrique  vrifier.
 * @param {number} maxAllowed - Le nombre maximum d'options de ce type autoris.
 * @return {Promise<boolean>} - Rsolu avec true si une nouvelle option peut tre insre, false sinon.
 */
export function canInsertNewOption(genericType, maxAllowed) {
    const apiUrl = `${BASE_URL}/options/generic_type_count/${genericType}`;

    return fetch(apiUrl)
        .then(response => {
            if (!response.ok) {
                throw new Error('Erreur rseau lors de la tentative de rcupration des donnes.');
            }
            return response.json();
        })
        .then(data => {
            if (data.count < maxAllowed) {
                return true;
            }
            return false;
        })
        .catch(error => {
            console.error('Il y a eu un problme avec l\'opration fetch:', error.message);
            return false;
        });
}
